export default{
	props:{
		show:{
			type:Boolean
		},
		records:{
			type:Array
		},
		price:{
			type:String
		},
		add_price:{
			type:Number
		},
		money:{
			type:String
		},
		start_time:{
			type:Number
		},
		end_time:{
			type:Number
		}
	},
	methods:{
		transferTime(time){
			var date = new Date(parseInt(time) * 1000);
			let m = date.getMonth()+1;
			m = m < 10 ? ('0' + m) : m;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let min = date.getMinutes();
			min = min < 10 ? ('0' + min) : min;
			let newDate = m+ "-" +d+ " " +h+ ":" + min;
			return newDate
		}
	}
}
